export default {
  data() {
    return {
    };
  },
  methods: {
    sendRequest() {
      this.$store.dispatch('loadProjects');
    },
    updateServersStatus() {
      this.$store.dispatch('getServersStatus');
      this.$store.dispatch('loadProjects');
    },
    statusCircle(code) {
      const divider = Math.floor(code / 100);
      switch (divider) {
        default:
          return 'status-circle red-circle';
        case 2:
          return 'status-circle green-circle';
        case 3:
          return 'status-circle orange-circle';
      }
    },
  },
  computed: {
    items() {
      return this.$store.getters.projects;
    },
    totalItems() {
      return this.$store.getters.projects;
    },
    headers() {
      return [{ text: 'Название', value: 'name' }, { text: 'URL', value: 'url', sortable: false }];
    },
  },
  created() {
    this.sendRequest();
  },
};



